.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.container{
  width:100%;
  height:100%;
  background-color: #f5f5f5;
  justify-content:center;
  flex-direction:row;
  align-items:center;
  margin-top:20px;
  overflow:auto;
  flex-wrap:wrap;
}

app-row{
  margin:10px;
  display:inline-block;
  background-color: white;
}

.key{
  font-weight:bold;
  font-color:red;
}
.value{
  font-weight:lighter;
}